@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&display=swap');


@media screen and (max-width: 600px) {
    
}
.navbar *{
    text-decoration: none;
    list-style: none;
    font-family: 'Caveat','I_Ngaan';
    color: #000;
}
.xin_logo {
    font-family: 'Leckerli One';
    font-size: 45px;
    position: absolute;
    top: 3px;
    left: 30px;
}
.barul1{
    margin-right: 30px;
    margin-top: 10px;
}
header{
    background-color: #85B2E0;
    text-align: right;
    position: fixed;
    width: 100%;
    top: 0;
    height: 65px;
}
.barul1{
    display: inline-block;
    font-size: 20px;
}
.barul1>li{
    position: relative;
    float: left;
    text-align: center;
    width: 100px;
    white-space: nowrap;
    color: #000;
}

.barul1 li{
    border-radius: 20px;
    text-decoration: none;
    line-height: 30px;
    text-align: center;
    padding: 8px 0px;
    width: 90px;
    color: #000;
}

.barul1 li:hover{
    background-color: #0080FF;
    
}
.barul1 a:hover{
    color: #FFFFFF;
}
.barul1 li:hover >a{
    color: #FFFFFF;
}

.barul1 a{
    width: 100%;
    height: 100%;
    display: block;
}

.barul1 li>ul>li{
    border: #000 0px solid;
    width: 95px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
    border-bottom: 1px solid #000;
    margin: 0 0 0 -35px;
}
.barul1 ul {
    border: #ccc 0px solid;
    border-radius: 20px;
    position: absolute;
    z-index: 99;
    top: 100%;
    left: -1px;
    display: none;
    text-align: center;
    /*background-color: rgba(255, 255, 255, 0.5);*/
    font-size: 18px;
    font-weight: 400;
    color: #000;
}
.barul1 li:hover>ul{
    display: block;
    opacity: 1;
    transform: opacity 0.3s;
    left: -1px;
    border-right: 5px;
    
}
.hightset{
    height: 60px;
}

