/*Leckerli One*/
@import url('https://fonts.googleapis.com/css2?family=Leckerli+One&display=swap');

/*width 小於850px 時要媒體*/
.about_main{
    display: flex;
    justify-content: center;
}

.aboutMe_text{
    text-align: center;
    font-size: 50px;
    color: #FFF;
    font-family: 'Leckerli One','Calibri Light','Caveat','I_Ngaan';
    font-weight: 200;
    margin: 15px 0 -20px;
}
.about_left{
	padding-top: 30px;
	margin-left: 20px;
	float: left;
    width: 30%;
    text-align: center;
}

.about_xinPhoto{
    height: 300px;
	border-radius: 20px;
}
.about_left_me{
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 20px;
}
.about_left_educationalQualifications{
    margin: 10px 0;
    padding: 10px;
    font-size: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
}
.about_left_contact{
    margin: 10px 0;
    padding: 10px;
    font-size: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
}
.about_left_contact p{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.about_left_professionalSkill{
    margin: 10px 0;
    padding: 10px;
    font-size: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
}
.about_left_professionalSkill img{
    width: 45px;
}

.about_right{
	margin-left: 25px;
    padding-top: 30px;
    margin-right: 20px;
	float: left;
    width: 60%;
    font-size: 20px;
    text-align: left;
}
.about_right_personal{
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 10px 20px;
    color: rgb(200, 251, 176);
}
.about_right ul{
    list-style: none;
    margin-left: -15px;
    margin-top: 0;
    margin: 0 0 10px -15px;
}
.about_right_study{
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 10px 20px;
    color: rgb(200, 251, 176);
    margin-top: 10px;
}
.about_right_prize{
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 10px 20px;
    color: rgb(200, 251, 176);
    margin-top: 10px;
}